import { Switch, Route } from 'react-router-dom';
import { HashLink } from "react-router-hash-link";

function App() {
  return (
    <>
      <Switch>
        <Route exact path="/">
          <HashLink to="/#home" id="test" smooth style={{ display: 'block', marginBottom: '1000vh' }}>Go</HashLink>
          <div id="home">Home</div>
          <HashLink to="/#about" smooth style={{ display: 'block', marginBottom: '1000vh' }}>Go</HashLink>
          <div id="about">About</div>
          <HashLink to="/#test" smooth style={{ display: 'block', marginBottom: '1000vh' }}>Go</HashLink>
        </Route>
      </Switch>
    </>
  );
}

export default App;
